#intro {
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    display: flex;
    justify-content: space-between; 
    align-items: center;
}

.bg {
    top: 9rem;
    right: 5rem;
    height: 70vh; 
    z-index: -1;
    position: absolute;
    object-fit: cover;
}

.introContent {
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 4rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
    z-index: 1;
}

.hello {
    font-size: 1.7rem;
    font-weight: 100;
}

.introName {
    color: rgb(226, 226, 15);
}

.introPara {
    font-size: large;
    font-weight: 200;
    letter-spacing: 1px;
}

.btn {
    display: flex;
    align-items: center;
    background: darkgrey;
    margin: 1rem 0;
    padding: 0.75rem 2rem;
    border: none;
    border-radius: 2rem;
}

.btnImg {
    object-fit: cover;
    margin: 0 0.15rem;
    height: 1.4rem;
}

@media screen and (max-width: 900px) {
    .bg {
        bottom: 0; /* Position it at the bottom */
        left: 50%; /* Center it horizontally */
        transform: translateX(-50%); /* Adjust for centering */
        position: absolute;
        height: 60vh; /* Adjust height if needed */
        width: 35vw; /* Adjust width for smaller screens */
        margin-top: 20rem;
    }

    #intro {
        height: auto; /* Adjust height for smaller screens */
        padding-bottom: 65vh; /* Add padding to account for bg image height */
        flex-direction: column; /* Stack content vertically */
        justify-content: flex-start; /* Align content to the top */
    }

    .introContent {
        height: auto; /* Adjust height for smaller screens */
        width: 100%; /* Ensure it takes the full width */
        padding: 1rem; /* Reduce padding for smaller screens */
        padding-top: 4rem; /* Ensure padding at the top */
        font-size: 3rem; /* Adjust font size for smaller screens */
    }

    .hello {
        font-size: 2rem; /* Adjust font size for smaller screens */
    }

    .introPara {
        font-size: medium; /* Adjust font size for smaller screens */
    }

    .btn {
        margin: 0.5rem 0; /* Reduce margin for smaller screens */
        padding: 0.5rem 1rem; /* Adjust padding for smaller screens */
    }
}
